  stringToHslColor(value) {
    var hash = 0;
    // param
    const str = value;
    const s = 50;
    const l = 50;
    for (var i = 0; i < str.length; i++) {
      hash = str.charCodeAt(i) + ((hash << 5) - hash);
    }

    var h = hash % 300;
    return "hsl(" + h + ", " + s + "%, " + l + "%)";
  }

